$font-display: "Lilita One", sans-serif;
$font-text: "Tilt Neon", sans-serif;
$font-menu:"Kode Mono", sans-serif;

/* SCSS RGB */
$amethyst: rgba(155, 93, 229, 1);
$brilliant-rose: rgba(241, 91, 181, 1);
$maize: rgba(254, 228, 64, 1);
$deep-sky-blue: rgba(0, 187, 249, 1);
$aquamarine: rgba(0, 245, 212, 1);

/* SCSS Gradient */
$gradient-top: linear-gradient(0deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-right: linear-gradient(90deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-bottom: linear-gradient(180deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-left: linear-gradient(270deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-top-right: linear-gradient(45deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-bottom-right: linear-gradient(135deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-top-left: linear-gradient(225deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-bottom-left: linear-gradient(315deg, #9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);
$gradient-radial: radial-gradient(#9b5de5ff, #f15bb5ff, #fee440ff, #00bbf9ff, #00f5d4ff);

$header-text-shadow-size: 5px;

body {
  background: url(./img/background.jpg) no-repeat center center fixed;
  animation: fluid 15s linear infinite;  
}

@keyframes fluid {
  from { background-position: 0 0; }
  to { background-position: 100% 0; }
}

header {
    h1 {
  font-family: $font-display;
  font-size: 3rem;
  color: $amethyst;
  -webkit-text-stroke: .5px white;
  text-shadow:
    calc($header-text-shadow-size * 1) calc($header-text-shadow-size * 1) 0 $deep-sky-blue,
    calc($header-text-shadow-size * 2) calc($header-text-shadow-size * 2) 0 $brilliant-rose,
    calc($header-text-shadow-size * 3) calc($header-text-shadow-size * 3) 0 $maize,
    calc($header-text-shadow-size * 4) calc($header-text-shadow-size * 4) 0 $aquamarine;
  transition: text-shadow 0.5s;
  text-align: center;
  margin: 0;
  padding: 1rem;
  &:hover {
    text-shadow:
      calc($header-text-shadow-size * 4) calc($header-text-shadow-size * 4) 0 $deep-sky-blue,
      calc($header-text-shadow-size * 8) calc($header-text-shadow-size * 8) 0 $brilliant-rose,
      calc($header-text-shadow-size * 12) calc($header-text-shadow-size * 12) 0 $maize,
      calc($header-text-shadow-size * 16) calc($header-text-shadow-size * 16) 0 $aquamarine;
  }
}
  img {
  position: absolute;
  top: 15;
  right: 30;
  height: 100px;
  width: 100px;
}
}

.bird {
  height: 100px;
  animation: run 10s linear infinite;
  position: absolute;
}

@keyframes run {
  0% {
    transform: translateX(-20vw);
  }
  100% {
    transform: translateX(120vw);
  }
}

nav {
  font-family: $font-menu;
  display: flex;
  justify-content: flex-start;

  ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    li {
      margin: .5rem 0;
      a {
        text-decoration: none;
        color: $amethyst;
        -webkit-text-stroke: .2px white;
        font-size: 1.5rem;
        font-weight: bolder;
        text-transform: uppercase;
        &:hover {
          color: $brilliant-rose;
        }
      }
    }
  }
}


